@import '../../../style/variables';

.single-comic {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 0.25fr 0.6fr 0.15fr;
    gap: 20px;
    align-items: start;
    @media (max-width: 767.98px){
        grid-template-columns: 1fr 1fr;
        margin-top: 30px;
    }
    @media (max-width: 500px){
        grid-template-columns: 1fr;
        margin-top: 15px;
    }
    &__img {
        @media (max-width: 767.98px){
            order: 1; 
        }
        position: relative;
        padding-bottom: 170%;
        @media (max-width: 500px){
           padding-bottom: 110%;
        }

        img{
            position: absolute;width: 100%;height: 100%;top: 0;left: 0;
            object-fit: cover;
        }
    }
    &__info {
        @media (max-width: 767.98px){
            order: 2;
        }
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
    }
    &__descr {
        font-size: 18px;
        line-height: 24px;
        margin-top: 25px;
        @media (max-width: 500px){
            margin-top: 5px;
        }
    }
    &__price {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        color: $main-color;
        margin-top: 25px;
        @media (max-width: 500px){
            margin-top: 5px;
        }
    }
    &__back {
        @media (min-width: 767.98px){
            justify-self: end; 
        }
        font-weight: bold;
        @media (max-width: 767.98px){
            order: 0;
            grid-column: span 2;  
        }
        @media (max-width: 500px){
            grid-column: span 1;  
        }
        font-size: 18px;
        line-height: 24px;
        &:hover {
            color: $main-color;
        }
    }
}